.servers-table {
  margin: auto;
  padding-top: 50px;
  td, th {
    padding: 10px 15px;
    border-collapse: collapse;
    border: 1px solid gray;
  }
  tr:nth-child(odd) {
    background-color: gray;
  }
  svg {
    vertical-align: middle;
  }
  &__icons {
    display: inline-flex;
    flex-direction: column;
    margin-left: 5px;
  }
  &__header {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.cursor-pointer {
  cursor: pointer;
}
.capitalize {
  text-transform: capitalize;
}